.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(#121212, #212b46);
}
.title {
  font-size: 4rem;
  color: white;
  animation: flipTitle 1s;
  text-align: center;
}
.containerInput {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 0.5rem;
  margin: 1rem 0;
  display: flex;
  border-radius: 0.5rem;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.5);
}
.containerInput input {
  background-color: transparent;
  border: 0;
  font: 1.2rem;
  color: #fff;
  outline: none;
  margin-right: 1rem;
}
.containerInput input::placeholder {
  color: #f1f1f1;
}
.buttonSearch {
  background-color: transparent;
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.5s;
}
.buttonSearch:hover {
  transform: scale(1.2);
}
.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: white;
  width: 14rem;
  border-radius: 0.5rem;
}
.main h2 {
  margin: 1rem 0;
  font-size: 1.8rem;
}
.main span {
  margin-bottom: 1rem;
  font-weight: bold;
}

@media (max-width: 700px) {
  .title {
    font-size: 2.5rem;
  }
  .main h2 {
    font-size: 1.5;
  }
}

@keyframes flipTitle {
  from {
    transform: rotateX(90deg);
  }
  to {
    transform: rotateX(0deg);
  }
}
